  },
  mounted() {
    axios
      .get(this.gsheet_url)
      .then((response) => this.parserAndDraw(response.data.feed.entry));
  },
  methods: {
    parserAndDraw(entries) {
      var dateList = [];
      var vinhList = [];
      var cuongList = [];
      var khaiList = [];
      var gamList = [];
      var sonList = [];
      var lanList = [];
      var lapList = [];
      var liemList = [];

      var vinhdaysAccList = [];
      var cuongdaysAccList = [];
      var khaidaysAccList = [];
      var gamdaysAccList = [];
      var sondaysAccList = [];
      var landaysAccList = [];
      var lapdaysAccList = [];
      var liemdaysAccList = [];
      var vinhstepsAccList = [];
      var cuongstepsAccList = [];
      var khaistepsAccList = [];
      var gamstepsAccList = [];
      var sonstepsAccList = [];
      var lanstepsAccList = [];
      var lapstepsAccList = [];
      var liemstepsAccList = [];

      let stats = [
        {
          name: "Vinh",
          steps: 0,
          days: 0,
        },
        {
          name: "Cường",
          steps: 0,
          days: 0,
        },
        {
          name: "Khải",
          steps: 0,
          days: 0,
        },
        {
          name: "Gấm",
          steps: 0,
          days: 0,
        },
        {
          name: "Sơn",
          steps: 0,
          days: 0,
        },
        {
          name: "Lan",
          steps: 0,
          days: 0,
        },
        {
          name: "Lập",
          steps: 0,
          days: 0,
        },
        {
          name: "Liêm",
          steps: 0,
          days: 0,
        },
      ];
      const self = this;
      entries.forEach(function (value) {
        if (value.content.$t !== "") {
          self.totalDay++;

          // Obtain date
          dateList.push(value.gsx$date.$t);

          let vinhStep = parseInt(value.gsx$vinh.$t) || 0;
          stats[0].steps += vinhStep;
          if (vinhStep > 0) {
            stats[0].days += 1;
          }
          vinhList.push(vinhStep);
          vinhdaysAccList.push(stats[0].days);
          vinhstepsAccList.push(stats[0].steps);

          let cuongStep = parseInt(value.gsx$cuong.$t) || 0;
          stats[1].steps += cuongStep;
          if (cuongStep > 0) {
            stats[1].days += 1;
          }
          cuongList.push(cuongStep);
          cuongdaysAccList.push(stats[1].days);
          cuongstepsAccList.push(stats[1].steps);
          let khaiStep = parseInt(value.gsx$khai.$t) || 0;
          stats[2].steps += khaiStep;
          if (khaiStep > 0) {
            stats[2].days += 1;
          }
          khaiList.push(khaiStep);
          khaidaysAccList.push(stats[2].days);
          khaistepsAccList.push(stats[2].steps);
          let gamStep = parseInt(value.gsx$gam.$t) || 0;
          stats[3].steps += gamStep;
          if (gamStep > 0) {
            stats[3].days += 1;
          }
          gamList.push(gamStep);
          gamdaysAccList.push(stats[3].days);
          gamstepsAccList.push(stats[3].steps);

          let sonStep = parseInt(value.gsx$son.$t) || 0;
          stats[4].steps += sonStep;
          if (sonStep > 0) {
            stats[4].days += 1;
          }
          sonList.push(sonStep);
          sondaysAccList.push(stats[4].days);
          sonstepsAccList.push(stats[4].steps);
          let lanStep = parseInt(value.gsx$lan.$t) || 0;
          stats[5].steps += lanStep;
          if (lanStep > 0) {
            stats[5].days += 1;
          }
          lanList.push(lanStep);
          landaysAccList.push(stats[5].days);
          lanstepsAccList.push(stats[5].steps);
          let lapStep = parseInt(value.gsx$lap.$t) || 0;
          stats[6].steps += lapStep;
          if (lapStep > 0) {
            stats[6].days += 1;
          }
          lapList.push(lapStep);
          lapdaysAccList.push(stats[6].days);
          lapstepsAccList.push(stats[6].steps);
          let liemStep = parseInt(value.gsx$liem.$t) || 0;
          stats[7].steps += liemStep;
          if (liemStep > 0) {
            stats[7].days += 1;
          }
          liemList.push(liemStep);
          liemdaysAccList.push(stats[7].days);
          liemstepsAccList.push(stats[7].steps);
        }
      });

      // Find winer
      this.winnerSteps = stats.reduce((max, obj) =>
        max.steps > obj.steps ? max : obj
      );
      console.log("this.winnerSteps", this.winnerSteps);
      this.winnerSteps["title"] =
        this.winnerSteps.name + " với " + this.winnerSteps.steps + " bước đi";
      this.winnerCommitment = stats.reduce((max, obj) =>
        max.days > obj.days ? max : obj
      );
      this.winnerCommitment["title"] =
        this.winnerCommitment.name +
        " với " +
        this.winnerCommitment.days +
        " ngày hoàn thành chỉ tiêu";

      let listSeries = [
        {
          name: "Vinh",
          data: vinhList,
        },
        {
          name: "Cường",
          data: cuongList,
        },
        {
          name: "Khải",
          data: khaiList,
        },
        {
          name: "Gấm",
          data: gamList,
        },
        {
          name: "Sơn",
          data: sonList,
        },

        {
          name: "Lan",
          data: lanList,
        },
        {
          name: "Lập",
          data: lapList,
        },
        {
          name: "Liêm",
          data: liemList,
        },
      ];

      this.lineChart.series = listSeries;
      let steps = [];
      let days = [];
      stats.forEach((element) => {
        steps.push(element.steps);
        days.push(element.days);
      });

      this.barChartSteps.series = [{ name: "Số bước", data: steps }];
      this.barChartCommit.series = [{ name: "Số ngày hoàn thành", data: days }];

      let stepsAccListSeries = [
        {
          name: "Vinh",
          data: vinhstepsAccList,
        },
        {
          name: "Cường",
          data: cuongstepsAccList,
        },
        {
          name: "Khải",
          data: khaistepsAccList,
        },
        {
          name: "Gấm",
          data: gamstepsAccList,
        },
        {
          name: "Sơn",
          data: sonstepsAccList,
        },

        {
          name: "Lan",
          data: lanstepsAccList,
        },
        {
          name: "Lập",
          data: lapstepsAccList,
        },
        {
          name: "Liêm",
          data: liemstepsAccList,
        },
      ];

      // Race chart
      this.barCharRaceSteps.data = {
        date: dateList,
        valueList: stepsAccListSeries,
      };

      let daysAccListSeries = [
        {
          name: "Vinh",
          data: vinhdaysAccList,
        },
        {
          name: "Cường",
          data: cuongdaysAccList,
        },
        {
          name: "Khải",
          data: khaidaysAccList,
        },
        {
          name: "Gấm",
          data: gamdaysAccList,
        },
        {
          name: "Sơn",
          data: sondaysAccList,
        },

        {
          name: "Lan",
          data: landaysAccList,
        },
        {
          name: "Lập",
          data: lapdaysAccList,
        },
        {
          name: "Liêm",
          data: liemdaysAccList,
        },
      ];

      this.barCharRaceCommit.data = {
        date: dateList,
        valueList: daysAccListSeries,
      };

      this.showRaceChart = true;
    },
  },
};
</script>
