<template>
      <div class="container">
        <div class="tile is-ancestor">
          <div class="tile is-parent is-9">
            <article class="tile is-child">
              <SearchBar @termChange="videoSearch($event)" />
              <div v-if="show">
                <VideoDetail :selectedVideo="selectedVideo" :videos="videos" />
              </div>
              <div v-else>
                <h4>...Loading ⟳</h4>
              </div>
            </article>
          </div>
          <div class="tile is-parent is-3 is-vertical">
            <article v-for="video in videos" :key="video.etag">
              <VideoList
                @selectedVideo="videoSelect($event)"
                :video="video"
                :imgUrl="video.snippet.thumbnails.medium.url"
                :videoTitle="video.snippet.title" />
            </article>
          </div>
        </div>
      </div> <!-- /.container -->
</template>

<script>
import YouTubeSearch from 'youtube-api-search';
import SearchBar from '@/components/SearchBar';
import VideoList from '@/components/VideoList';
import VideoDetail from '@/components/VideoDetail';
import {getRandomSong} from '@/util'
export default {
  name: 'Youtube',
  data() {
    return {
      show: false,
      video: null,
      videos: [],
      selectedVideo: null,
      YOUTUBE_API_KEY: "AIzaSyCGIjQH5eLZJ0OFWsn-7XrK5UqNJDzcB2I"
    }
  },
  components: {
    SearchBar,
    VideoList,
    VideoDetail,
  },
  created() {
    // init search
    this.videoSearch(getRandomSong());
  },
  methods: {
    videoSearch(searchTerm) {
      YouTubeSearch( {key: this.YOUTUBE_API_KEY, term: searchTerm}, (videos) => {
        this.videos = videos;
        // get the first video (before one is selected --this is the default)
        this.selectedVideo = videos[0];
        this.show = true;
      });
    },
    videoSelect(video) {
      this.selectedVideo = video;
    }
  },
};
</script>